/*  Weekly Challenge Content Header
*
*  Version:
*    1.0.0 - 2020/12/23
*/

.c-weekly-challenge-content {
  &__inner {
    padding-top: 4.375rem;
    padding-bottom: 4.375rem;
    max-width: 125rem;
  }

  &__pretitle {
    text-align: center;
    font-weight: 700;
  }

  &__title {
    text-align: center;
    margin-top: 1.25rem;
  }

  &__posts {
    margin-top: 2.625rem;
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      padding-top: 10.3125rem;
    }

    &__posts {
      margin-top: 5.0625rem;
    }
  }
}