.c-challenge-post {
  &__item {
    padding-top: 2.5rem;
    @include t-gutters(true);
    position: relative;

    &:not(:last-of-type) {
      margin-bottom: 4.375rem;
    }
  }

  &__blue-background {
    display: block;
    background-color: $s-color--background-tertiary;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding-bottom: 54%;
  }

  &__images {
    display: flex;
    justify-content: center;
  }

  &__image {
    flex: 1;

    &-container {
      width: 49%;
      flex-shrink: 0;
      position: relative;
    }
  }

  &__content-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__title {
    margin-bottom: 1.25rem;
  }

  &__user {
    color: $s-color--font-8;
    margin-bottom: 2rem;
  }

  &__content {
    color: $s-color--font-8;
    line-height: 1.125rem;
    margin-bottom: 4rem;
  }

  &__related-products {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__related-product {
    display: block;

    &-image {
      &-container {
        background-color: $s-color--background-tertiary;
        height: 5rem;
        width: 3.75rem;
        position: relative;
        margin-bottom: 0.625rem;
        line-height: 1.3;
      }

      height: 75%;
      width: auto;
      @include t-absolute-center-x-y;

      &-icon {
        background: white;
        border-radius: 100%;
        @include t-absolute-center-x-y;
        width: 1.8125rem;
        height: 1.8125rem;
        transition: all $s-anim--time-med;

        svg {
          @include t-absolute-center-x-y;
          @include t-width-and-height(10);
          transition: all $s-anim--time-med;
        }
      }
    }

    &-details {
      color: $s-color--font-1;
    }

    &:hover {
      .c-challenge-post__related-product-image-icon {
        background-color: $s-color--font-1;

        svg path {
          fill: white;
        }
      }
    }
  }

  @include t-media-query($s-small-down) {
    &__item {
      padding-top: 2.5rem;
    }

    &__images {
      margin-bottom: 1.25rem;
    }

    &__content-container {
      margin-top: 3rem;
      text-align: center;
      align-items: center;
    }

    &__related-products {
      justify-content: center;
    }

    &__related-product {
      display: flex;
      flex-direction: column;
      align-items: center;

      &:nth-of-type(2) {
        margin: 1.5rem 0 0 2rem;
      }

      &-image-container {
        height: 7.875rem;
        width: 5.75rem;
      }
    }
  }

  @include t-media-query($s-small-up) {
    &__item {
      padding-top: 3.5rem;
      display: flex;
      justify-content: space-between;

      &:not(:last-of-type) {
        margin-bottom: 5.625rem;
      }
    }

    &__blue-background {
      width: 38.55%;
      bottom: 0;
      padding: unset;
      height: 100%;

      .is-right & {
        left: unset;
        right: 0;
      }

      .is-left & {
        left: 0;
      }
    }

    &__images {
      width: 43.44%;
      margin-right: 6.1%;
      flex-shrink: 0;

      .is-right & {
        order: 2;
        margin-left: 6.1%;
        margin-right: unset;
      }
    }

    &__content-container {
      width: 45%;

      .is-right & {
        order: 1;
      }
    }

    &__related-product {
      display: flex;
      align-items: center;
      margin-right: 1.125rem;

      &:last-child {
        margin-right: unset;
      }

      &-image {
        &-container {
          margin-right: 1.125rem;
        }
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &__item {

      &:not(:last-of-type) {
        margin-bottom: 7.5rem;
      }
    }

    &__user {
      transition: .3s;

      &:hover {
        color: $s-color--font-1;
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__item {
      margin-bottom: 10rem;
      padding-top: 6.5rem;
      justify-content: flex-end;

      &:not(:last-of-type) {
        margin-bottom: 16.75rem;
      }

      &.is-left {
        justify-content: flex-start;
      }
    }

    &__blue-background {
      width: 35%;
      padding-bottom: 44.5%;
    }

    &__content-container {
      display: block;
      margin-top: 7.0625rem;
      width: 37%;
    }

    &__title {
      margin-bottom: 2.0625rem;
    }

    &__content {
      margin-top: 1.625rem;
      max-width: 34.875rem;
      line-height: 1.625rem;
      margin-bottom: 5.6875rem;
    }

    &__images {
      width: 47.44%;
      margin-right: 8.1%;

      .is-right & {
        margin-left: 12.6%;
      }

      .is-left & {
        margin-right: 16%;
      }
    }

    &__related-product {
      &-image {
        &-container {
          height: 8.25rem;
          width: 6rem;
          margin-right: 1.3125rem;
        }
      }
    }
  }
}